<template>
	<div class="tasks">
		<h1 class="tasks__title">{{ lang[curLang].title }}</h1>
		<form class="tasks__form">
			<input
				:placeholder="lang[curLang].placeholder"
				@keyup.enter="addTask"
				v-model="newTask"
			/>
			<button @click="addTask">+</button>
		</form>
		<ul class="tasks__list">
			<li
				class="tasks__list-li"
				v-for="(task, index) in state.tasks"
				:key="index"
			>
				<div class="tasks__list-text">
					<input type="checkbox" id="check" />
					<label for="check"></label>
					<p :class="{ done: task.completed }" @click="toggleComplete(task)">
						{{ task.text }}
					</p>
				</div>
				<a href="#!" class="tasks__list-delete" @click="removeTask(index)"
					>{{ lang[curLang].delete }}
					<img src="../assets/images/bin.png" alt=""
				/></a>
			</li>
		</ul>
		<div class="tasks__control">
			<a href="" class="tasks__control-clear" @click="clearCompleted">
				{{ lang[curLang].completed }}
			</a>
			<a href="" class="tasks__control-clear" @click="clearAll">
				{{ lang[curLang].all }}
			</a>
		</div>
		<h3 class="tasks__info">{{ lang[curLang].pending }} {{ completed }}</h3>
	</div>
</template>

<script setup>
import {
	state,
	lang,
	addTask,
	toggleComplete,
	removeTask,
	clearCompleted,
	clearAll,
} from '@/store';
import { computed } from 'vue';
const curLang = computed(() => state.curLang);
const completed = computed(() => state.completed);
const newTask = computed({
	get: () => state.newTask,
	set: val => (state.newTask = val),
});
</script>
