import { reactive, watchEffect } from 'vue';
const state = reactive({
	newTask: '',
	tasks: localStorage.tasks ? JSON.parse(localStorage.tasks) : [],
	langs: ['RU', 'EN'],
	curLang: localStorage.lang ? localStorage.lang : 'RU',
	completed: 0,
});

watchEffect(()=>{
	state.completed = state.tasks.filter(task => !task.completed).length
})

// state.tasks && state.tasks.forEach(task => !task.completed ? state.completed++ : 0)

const lang = reactive({
	RU: {
		title: 'Список дел',
		placeholder: 'Добавь задание...',
		completed: 'Удалить выполненные',
		all: 'Удалить все',
		delete: 'Удалить',
		pending: 'Оставшиеся задания: ',
	},
	EN: {
		title: 'To Do List',
		placeholder: 'Add a task...',
		completed: 'Clear completed',
		delete: 'Remove',
		all: 'Clear all',
		pending: 'Pending tasks: ',
	},
});
const save = () => (localStorage.tasks = JSON.stringify(state.tasks));

const addTask = () => {
	if (state.newTask.trim().length > 2) {
		state.tasks.push({ text: state.newTask.trim(), completed: false });
		state.newTask = '';
		save();
	}
};

const removeTask = index => {
	state.tasks.splice(index, 1);
	save();
};

const toggleComplete = task => {
	task.completed = !task.completed;
	save();
};

const clearCompleted = () => {
	state.tasks = state.tasks.filter(task => !task.completed);
	save();
};

const clearAll = () => {
	state.tasks = [];
	save();
};

export {
	state,
	lang,
	addTask,
	toggleComplete,
	clearAll,
	clearCompleted,
	removeTask,
};
