<template>
	<select class="header__select" @change="changeLan($event.target.value)">
		<option
			v-for="lan in state.langs"
			:key="lan"
			:selected="lan === state.curLang"
		>
			{{ lan }}
		</option>
	</select>
	<div class="container">
		<Tasks />
	</div>
</template>

<script setup>
import Tasks from './components/Tasks.vue';
import './assets/styles/main.scss';
import { state } from './store';
import { computed } from 'vue';
const curLang = computed(() => state.curLang);
function changeLan(lan) {
	state.curLang = lan;
	localStorage.lang = lan;
}
</script>

<style lang="scss">
.header__select {
	position: fixed;
	display: block;
	padding: 10px;
	font-weight: 500;
	font-size: 17px;
	top: 50px;
	right: 50px;
	border-radius: 7px;
}
</style>
